<template>
    <keep-alive>
        <panel :config="config" class="activity">
            <template slot="header">&nbsp;<span v-if="user">{{ title }}</span></template>
            <template slot="body">
                <template v-if="activities === null"><LoadingComp></LoadingComp></template>
                <template v-else>
                    <template v-if="activities.length === 0">
                        <div><p>{{ getTrans('noplay') }}</p></div>
                    </template>
                    <div class="navigation" v-if="pageCount > 1">
                        <pagination @navigate="getPaginateActivities" :current-page="currentPage" :page-count="pageCount" :lang="lang" :size="1"></pagination>
                    </div>
                    <div class="grid-activities">
                        <template v-if="activities.length > 0">
                            <div v-for="activity in currentActivities" v-if="games[activity.game_id]" :key="activity.client_id" :id="'activity-' + activity.game_id" class="activity clickable">
                                <a :href="gameUrl(activity.game_id)" @click.prevent.stop="goToGame(activity.game_id)" class="icon" :key="activity.client_id" :id="'activity-' + activity.game_id" ><img v-if="games[activity.game_id].icon" :src="games[activity.game_id].icon" class="icon game" @error="gameIconError"/></a>
                                <a :href="gameUrl(activity.game_id)" @click.prevent.stop="goToGame(activity.game_id)" class="rank" style="" :key="activity.client_id" >
                                    <div @click.prevent.stop="goToGame(activity.game_id)">
                                        <ul>
                                            <li class="game name">{{ games[activity.game_id].name }}</li>
                                            <li>
                                                <template v-if="['md', 'lg', 'xl'].includes($mq)">
                                                    {{ getTrans('last_used') }}&nbsp;
                                                </template>
                                                <time :date="activity.last_used">{{ getLastUsed(activity.last_used) }}</time>
                                            </li>
                                            <my-ranking :game="activity.game_id" :config="{expandedlist: true, userId: userProfileId, chevron: false, trans: config.trans, urls: config.urls}"></my-ranking>
                                        </ul>
                                    </div>
                                </a>
                                <a :href="gameUrl(activity.game_id)" @click.prevent.stop="goToGame(activity.game_id)" class="achievements" :key="activity.client_id" v-if="!['xxs', 'xs', 'xsm', 'sm'].includes($mq)">
                                    <ul class="list-inline achievements" v-if="Object.keys(achievements).length > 0 && achievements[activity.game_id] && hasAtLeastOneValidAchievement(achievements[activity.game_id])">
                                        <li v-for="ach in achievements[activity.game_id].slice(0, getMaxAchievements(activity.game_id))" :key="ach.id">
                                            <img :src="forceHTTPS(getAchievementIcon(ach))" class="achievement icon small" :class="getAchievementIconClass(ach)" v-tooltip.bottom="getAchievementDescription(ach, $moment.unix(ach.awarded_ts).format('LLLL'))"/>
                                        </li>
                                        <li v-if="getMaxAchievements(activity.game_id) < achievements[activity.game_id].length" class="and-other-achievements">{{ getTransChoice(getLeftAchievements(activity.game_id), 'and_other_achievements', {'%count%': getLeftAchievements(activity.game_id)}) }}</li>
                                    </ul>
                                </a>
                                <a :href="gameUrl(activity.game_id)" @click.prevent.stop="goToGame(activity.game_id)" class="chevron" :key="activity.client_id" >
                                    <div class="chevron-action"><font-awesome-icon icon="chevron-right" size="3x" /></div>
                                </a>
                            </div>
                        </template>
                        <template v-if="notOwnedGames">
                            <template v-if="(currentActivities.length > 0 || currentPage === 1) && currentNotOwnedGames.length > 0">
                                <hr v-if="currentActivities.length > 0"/>
                                <div class="notOwnedGameTitle"><h4>{{ getTrans('other_games') }}</h4></div>
                            </template>
                            <div v-for="gid in currentNotOwnedGames" :key="gid" :id="'notOwnedGame-' + gid" class="activity not-owned-game clickable">
                                <a :href="gameUrl(gid)" @click.prevent.stop="goToGame(gid)" class="icon"><img v-if="games[gid].icon" :src="games[gid].icon" class="icon game" @error="gameIconError"/></a>
                                <a :href="gameUrl(gid)" @click.prevent.stop="goToGame(gid)" class="stores" style="">
                                    <div @click.prevent.stop="goToGame(gid)">
                                        <ul>
                                            <li class="game name">{{ games[gid].name }}</li>
                                            <li><game-short-desc :gameId="gid"></game-short-desc></li>
                                        </ul>
                                    </div>
                                </a>
                                <a :href="urlFor('game-index', {'id': gid})" :target="isIn3rdParty ? '_blank' : '_self'" class="chevron">
                                    <div class="chevron-action"><font-awesome-icon icon="chevron-right" size="3x" /></div>
                                </a>
                            </div>
                        </template>
                        <div class="navigation" v-if="pageCount > 1">
                            <pagination @navigate="getPaginateActivities" :current-page="currentPage" :page-count="pageCount" :lang="lang" :size="1"></pagination>
                        </div>
                    </div>
                </template>
            </template>
        </panel>
    </keep-alive>
</template>

<script>
import axios from 'axios';
import HTTP from '@/httpapi';
import Games from '@api/games';
import User from '@api/user';
import GenericMixin from '@mixins/generic';
import MyRanking from '@/components/MyRanking';
import Pagination from '@/components/Pagination';
import AchievementMixin from '@mixins/achievements';
import Achievement from '@classes/achievement';
import GameShortDesc from '@/components/Game/ShortDesc';

const MAX_ACHIEVEMENT_COUNT = 2;
const MAX_ACHIEVEMENT_COUNT_BIG_SIZE = 8;

// TODO check webpack handles common chunks correctly so that we can safely
// remove the following imports
// import bus from '@/event_bus'
// import LoadingComp from '@/components/LoadingComp'

export default {
    name: 'user_activities',

    mixins: [GenericMixin, AchievementMixin],

    computed: {
        title () {
            if (this.user) {
                return this.getTrans(
                    this.$options.name + (!this.publicDisplay ? '' : '_public') + '_title',
                    {'%login%': this.user.login_name}
                );
            }
        },

        displayWidth: function () {
            return (this.config.width || typeof this.config.width !== 'string' || !this.config.width.include('px') || parseInt(this.config.width) >= 327 ? this.config.width : '327px');
        },

        pageCount () {
            if (!this.activities && !this.notOwnedGames) {
                return 0;
            }

            return Math.ceil((this.activities.total + this.notOwnedGames.length) / this.itemsPerPage);
        },

        currentActivities () {
            if (!this.activities) {
                return [];
            }

            return this.activities.slice((this.currentPage - 1) * this.itemsPerPage, this.currentPage * this.itemsPerPage);
        },

        currentNotOwnedGames () {
            if (!this.notOwnedGames && !this.activities) {
                return [];
            }

            let ca = this.currentActivities.length;
            if (ca >= this.itemsPerPage) {
                return [];
            }

            let aa = this.activities.length;

            let begin = Math.max(((this.currentPage - 1) * this.itemsPerPage) + ca - aa, 0);

            let nog = this.notOwnedGames.slice(begin, begin + (this.itemsPerPage - ca));

            return nog;
        },

        userProfileId () {
            return this.config.userProfile.id;
        },

        publicDisplay () {
            return (this.userProfileId !== 'me');
        }
    },

    components: {
        'pagination': Pagination,
        'my-ranking': MyRanking,
        'game-short-desc': GameShortDesc
    },

    methods: {
        getActivities (page) {
            let t = this;
            axios.all([
                User.details(this.userProfileId, 'userapps', 'achievements'),
                Games.all(),
                Games.rankables()
            ]).then(axios.spread(function (details, games, rankables) {
                t.rankables = rankables.data.rankingGames;
                t.games = games.data.games;

                // sort achievements by awarded_ts DESC in each game
                let achs = details.data.user.achievements;
                Object.keys(achs).forEach((gid) => {
                    achs[gid].sort((a, b) => (a.awarded_ts > b.awarded_ts ? -1 : (a.awarded_ts < b.awarded_ts ? 1 : 0)));
                    achs[gid] = achs[gid].map((el) => new Achievement(el)).filter((el) => !el.isAlwaysHidden());
                });
                t.achievements = achs;

                let allActivities;
                t.user = details.data.user;
                allActivities = details.data.user.userapps;

                // filter games
                let ownedGames = {};
                allActivities = allActivities.filter(function (act) {
                    if (t.games[act.game_id]) {
                        ownedGames[act.game_id] = true;
                        return true;
                    }
                });
                allActivities.total = allActivities.length;

                let notOwnedGames = [];
                Object.keys(t.games).forEach(function (gid) {
                    if (!ownedGames[gid] && t.games[gid].status !== 'draft') {
                        notOwnedGames.push(gid);
                    }
                });
                t.notOwnedGames = notOwnedGames;

                t.activities = allActivities;

                t.getPaginateActivities();
            }));
        },

        isRankable (gameId) {
            return (this.rankables && this.rankables.findIndex(function (gid) { return gid === gameId; }) >= 0);
        },

        getPaginateActivities (page) {
            if (page) {
                this.currentPage = page;
            }
        },

        getMaxAchievements (gameId) {
            let c = this.achievements[gameId].length;
            let max = ['md', 'lg', 'xl'].includes(this.$mq) ? MAX_ACHIEVEMENT_COUNT_BIG_SIZE : MAX_ACHIEVEMENT_COUNT;

            return (c > max ? max : c);
        },

        getLeftAchievements (gameId) {
            return this.achievements[gameId].length - this.getMaxAchievements(gameId);
        },

        getLastUsed (timestamp) {
            if (['md', 'lg', 'xl'].includes(this.$mq)) {
                return this.$moment.unix(timestamp).format('LLLL');
            } else {
                return this.$moment.unix(timestamp).format('L HH:mm');
            }
        }
    },

    mounted () {
        HTTP.ifInitialized(this.getActivities);
    },

    data () {
        return {
            itemsPerPage: 5,
            currentPage: 1,
            activities: null,
            achievements: null,
            games: null,
            notOwnedGames: null,
            rankables: null,
            user: null
        };
    }
};

</script>

<style lang='scss' scoped>
@import '~sass/_variables.scss';
@import '~sass/image.scss';

.panel.activity /deep/ {
    padding: 1em;

    div.pagination {
        margin: 0 auto;
    }

    div.grid-activities {
        div.activity {
            display: grid;
            grid-template-columns: minmax(50px, 100px) minmax(max-content, 1fr) minmax(60px, 1.5fr) 30px;
            grid-auto-rows: 100px;
            margin-bottom: 1em;

            &.not-owned-game {
                grid-template-columns: minmax(50px, 100px) minmax(max-content, 1fr) 30px;

                .icon.game {
                    filter: grayscale(100%);
                }
            }

            a {
                color: #333;
                text-decoration: none;

                &.rank, &.achievements, &.stores {
                    padding-left: 1em;
                }

                &.chevron {
                    position: relative;
                }

                img.icon.game {
                    max-width: 100%;
                    max-height: 100%;
                }
            }

            .chevron-action {
                color: lightgray;
                text-align: center;
                position: absolute;
                margin: auto;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;

                svg {
                    position: absolute;
                    margin: auto;
                    top: 0;
                    bottom: 0;
                    left: 0;
                    right: 0;
                }
            }

            &:hover {
                .chevron-action {
                    color: white;

                    &:hover {
                        background-color: lightgray;
                    }
                }
            }

            ul {
                list-style: none;
                padding: 0;
                text-decoration: none;

                li {
                    font-size: 0.9em;

                    &.name {
                        font-weight: 500;
                        font-size: 1.1em;
                    }
                }

                &.list-inline {
                    &.achievements {
                        li {
                            min-height: 30px;
                            min-width: 30px;
                            max-height: 60px;
                            max-width: 80px;

                            &.and-other-achievements {
                                color: map-get($asnet-colors, light-purple);
                                font-size: 0.9em;
                                vertical-align: middle;
                                max-width: none;
                            }
                        }

                        img.icon.achievement {
                            margin: 5px 0;
                        }
                    }
                }
            }

            .myranking {
                top:0;

                .smallsize:nth-of-type(3).onpodium.ranked span:not(.karma) {
                    top: -5px;
                }
                .smallsize:nth-of-type(3).hasprogress.ranked span:not(.karma) {
                    top: 3px;
                }

                span {
                    top:0;
                }

                .fanet {
                    top: -8px;
                }

                .progress {
                    height: 15px;
                    margin-bottom: 0;
                    position: relative;
                    top: 4px;
                }
            }
        }
    }

    div.notOwnedGameTitle {
        display: flex;
        flex-wrap: nowrap;
        justify-content: center;

        h4 {
            color: map-get($asnet-colors, dark-purple);
            font-size: 1.2em;
        }
    }
}

@include media-breakpoint-down(sm) {
    .panel.activity {
        div.grid-activities {
            div.activity {
                grid-template-columns: 30px minmax(max-content, 1fr) 30px;

                &.not-owned-game {
                    grid-template-columns: 30px minmax(min-content, 1fr) 30px;
                }

                a {
                    img.icon.game {
                        max-height: $icon-max-height-sm;
                        max-width: $icon-max-width-sm;
                    }
                }
            }
        }
    }
}
</style>



