<template>
    <div class="channelslink" v-if="product">
        <a v-for="channel in channelsSorted" v-if="validChannel(channel.channel)" :key="channel.channel" :href="getTrackedStoreUrl(channel)" target="_blank" @click.prevent.stop="prev"><img :src="getStoreImageUrl(channel.channel)" @error="genericImageError" class="channel" :class="channel.channel + '_icon'"/></a>
    </div>
</template>

<script>
import HTTP from '@/httpapi';
import Games from '@api/games';
import GenericMixin from '@mixins/generic';

export default {
    name: 'game-short-desc',

    props: ['gameId'],

    mixins: [GenericMixin],

    computed: {
        channelsSorted () {
            let channels = Object.values(this.product.localization.digitals).filter((v) => typeof v === 'object');
            channels.sort(function (a, b) {
                if (a.channel > b.channel) {
                    return 1;
                }
                if (a.channel < b.channel) {
                    return -1;
                }

                return 0;
            });

            return channels;
        }
    },

    methods: {
        getProductDescription () {
            let that = this;
            Games.crosspromo(this.gameId, this.lang).then(function (product) {
                if (product) {
                    that.product = product.data.product;
                }
            }).catch((e) => void e);
        },

        validChannel (channelName) {
            return (channelName !== 'macstore' && channelName !== 'asmodee');
        },

        getStoreImageUrl (channelName) {
            return this.baseISImageHost + 'stores/' + channelName + '/' + this.lang + (channelName === 'appstore' ? '.svg' : '.png');
        },

        prev (event) {
            window.open(event.path[1].href);
        },

        getTrackedStoreUrl (channel) {
            if (this.product.localization.digitals[channel.channel]) {
                return this.product.localization.digitals[channel.channel];
            }

            return channel.url;
        }
    },

    mounted () {
        HTTP.ifInitialized(this.getProductDescription);
    },

    data () {
        return {
            product: null
        };
    }
};
</script>

<style lang="scss" scoped>
@import '~sass/_variables.scss';

.channelslink {
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    margin-top: 10px;
    width: 98%;

    img.channel {
        max-width: 135px;
        margin-left: 10px;

        &.googleplay_icon {
            margin-left: 0;
            max-width: 155px;
            position: relative;
            top: -9px;
        }

        &.amazonstore_icon {
            max-width: 119px;
        }
    }
}

@include media-breakpoint-down(sm) {
    .channelslink {
        flex-wrap: wrap;
        zoom: .5;

        a {
            img {
                margin-bottom: 5px;
            }
        }
    }
}
</style>



